import React, { Component } from 'react'
import { observer } from 'mobx-react'

const Todos = ({store, gc}) => {
    return (
        <div>
            {store.todos.map((el, i) => {
                return (
                    <div key={gc() + el.id + gc() + i} className={"col-sm-3 " + gc()}>
                        <p>
                            <span className="r" onClick={() => { store.del(i, el.id) }}>&times;</span>
                                {el.task}
                        </p>
                    </div>
                )
            })}
        </div>
    )
}

const TodoForm = ({addTodo}) => {
    let task
    return (
        <div className={"form-group"}>
            <input type="text" ref={(t) => {task = t}} className="form-control" placeholder="add Todo item" />
            <button className="btn btn-default" onClick={() => {addTodo(task.value)}}>addTask</button>
        </div>
    )
}

class App extends Component {
    constructor(props) {
        super(props)
        this.store = this.props.store
        this.colors = ["red", "blue", "yellow", "green", "gray"]
    }
    getColor = () => {
        return this.colors[Math.floor((Math.random() * 4) + 1)]
    }
    render() {
        if(this.store.todos.length > 0) {
            return (
                <div className="row">
                    <TodoForm addTodo={this.store.add} />
                    <Todos store={this.store} gc={this.getColor} />
                </div>
            )
        }else {
            return (
                <div></div>
            )
        }
    }
}


