import React from 'react'
import ReactDOM from 'react-dom'
import App from './App'
import { observable } from 'mobx'
import axios from 'axios'

window.store = observable({
    todos: []
})

window.store.del = (i, id) => {
    axios
        .delete(`http://58a9a4b407165e12007cb03a.mockapi.io/v1/todo/${id}`)
    window.store.todos.splice(i, 1)
}

window.store.add = (s) => {
    axios.post('http://58a9a4b407165e12007cb03a.mockapi.io/v1/todo', {task: s})
    window.store.todos.push({task: s})
}

window.store.reset = (data = []) => {
    window.store.todos = data
    console.log(data)
}

axios.get('http://58a9a4b407165e12007cb03a.mockapi.io/v1/todo')
    .then((data) => {
        window.store.reset(data.data.reverse())
    })

ReactDOM.render(
  <App store={window.store} />
    ,
  document.getElementById('root')
)


